import React from 'react';
import { Link } from 'gatsby';
import styled from '@emotion/styled';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { Layout, Container } from 'layouts';
import { Header } from 'components';
import config from '../../config/site';

const StyledLink = styled(Link)`
  color: ${props => props.theme.colors.white.light};
  padding: 5px 10px;
  border: solid 1px #fff;
  &:hover {
    color: ${props => props.theme.colors.black.blue};
    background: ${props => props.theme.colors.white.light};
  }
`;

const Information = styled.div`
  text-align: center;
  a {
    display: inline-block;
    color: ${props => props.theme.colors.black.base};
    box-shadow: inset 0 -2px 0 ${props => props.theme.colors.primary.base};
    border-bottom: 1px solid ${props => props.theme.colors.primary.base};
    transition: ${props => props.theme.transitions.default.transition};
    text-decoration: none;
    padding: 0 2px;
    margin-bottom: 0.5rem;
    &:hover,
    &:focus {
      background: ${props => props.theme.colors.primary.base};
      color: ${props => props.theme.colors.white.light};
    }
    h3 {
      font-size: 1.2rem;
      margin-bottom: 0.25rem;
    }
  }
`;

const Tag = ({ pageContext }) => {
  const { posts = [], tagName } = pageContext;
  const upperTag = tagName.charAt(0).toUpperCase() + tagName.slice(1);
  const totalCount = posts.length;
  const tagHeader = `${totalCount} post${
    totalCount === 1 ? '' : 's'
  } tagged with "${upperTag}".`;
  return (
    <Layout>
      <Helmet title={`${config.title} | ${upperTag}`} />
      <Header title={upperTag}>
        <StyledLink to="/tags">All Tags</StyledLink>
      </Header>
      <Container>
        <Information>
          <p>{tagHeader}</p>
          {posts.map((post, index) => (
            <div>
              <Link key={index} to={post.frontmatter.path}>
                <h3>{post.frontmatter.title}</h3>
              </Link>
            </div>
          ))}
        </Information>
      </Container>
    </Layout>
  );
};

export default Tag;

Tag.propTypes = {
  pageContext: PropTypes.shape({
    posts: PropTypes.array,
    tagName: PropTypes.string,
  }),
};
